@use "global" as *;
.article {
  @include mq(md) {
  }
}

.article__layout {
  margin-top: rem(24);
  margin-right: rem(20);
  margin-left: rem(20);
  margin-bottom: rem(101);
  @include mq(md) {
    display: flex;
    margin-right: rem(119);
    margin-left: rem(119);
    margin-bottom: rem(150);
  }
}

.article__items {
  display: flex;
  @include mq(md) {
  }
}

.article__items-layout {
  display: flex;
  align-items: center;
  margin-bottom: rem(16);
  @include mq(md) {
    margin: 0;
  }
}

.article__items time {
  font-family: "Noto Sans JP", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: rem(16);
  line-height: 1.4375;
  color: #333;
  transition: 0.7s;
  @include mq(md) {
  }
}

.article__category {
  font-family: "Noto Sans JP", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: rem(14);
  line-height: 1.4285;
  color: #333;
  border: 1px solid #fff;
  transition: 0.7s;
  @include mq(md) {
  }
}

.article__category-layout {
  width: rem(99);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: rem(32);
  padding: rem(6) 0;
  @include mq(md) {
    margin-left: rem(48);
  }
}

.article__text {
  font-family: "Noto Sans JP", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: rem(16);
  line-height: 1.4375;
  color: #333;
  transition: 0.7s;
  @include mq(md) {
  }
}

.article__text-layout {
  margin-bottom: rem(28);
  display: block;
  @include mq(md) {
    display: flex;
    align-items: center;
    margin: 0;
    margin-left: rem(61);
  }
}

.article__text-inner {
  @include mq(md) {
  }
}

.article__text-layout p {
  @include mq(md) {
    // underline
    display: inline;
    background-image: linear-gradient(90deg, #f00, #f00);
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: 0 1px;
    transition: background-size 0.7s;
  }
}

.article__text-layout p:hover {
  @include mq(md) {
    background-size: 100% 1px;
    transition: background-size 0.7s;
  }
}

.article__btn {
  background-color: #333;
  text-align: center;
  font-family: "Noto Sans JP", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: rem(20);
  line-height: 1.45;
  letter-spacing: rem(2);
  margin: 0 auto;
  // border: 1px solid #fff;
  @include mq(md) {
    font-weight: 500;
    font-size: rem(16);
    line-height: 1.4375;
  }
}

.article__btn-layout {
  display: block;
  max-width: rem(335);
  width: 100%;
  overflow: hidden;
  position: relative;
  @include mq(md) {
    max-width: rem(134);
    margin-top: auto;
    margin-bottom: auto;
    margin-left: rem(48);
    margin-right: rem(0);
  }
}

.article__btn-layout span {
  display: block;
  position: relative;
  color: #fff;
  z-index: 5;
  width: 100%;
  height: 100%;
  border: 1px solid #fff;
  padding: rem(10) 0;
  @include mq(md) {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
}

.article__btn-layout span:hover {
  position: relative;
  color: #333;
  z-index: 99;
  width: 100%;
  height: 100%;
  @include mq(md) {
  }
}

.article__btn::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 1);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
  transition: 0.7s;
  @include mq(md) {
  }
}

.article__btn:hover::after {
  transform: translateX(0%);
  transition: 0.7s;
  @include mq(md) {
  }
}
